const LOGGED_IN = "hangperson/user/LOGGED_IN";
const LOG_OUT = "hangperson/user/LOG_OUT";
const LOG_OUT_SUCCESS = "hangperson/user/LOG_OUT_SUCCESS";
const LOG_OUT_FAIL = "hangperson/user/LOG_OUT_FAIL";

export default (state = {}, action) => {
  switch (action.type) {
    case LOGGED_IN:
      let user = action.payload.data;
      return user;
    case LOGGED_OUT:
      return {};
    default:
      return state;
  }
};

export const logOut = () => {
  const req = axios.delete("/api/session");

  return {
    type: LOGGED_OUT,
    payload: req
  };
};

export const getCurrentUser = () => {
  const req = axios.get("/api/user/current");

  return {
    type: LOGGED_IN,
    payload: req
  };
};



